import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { Log } from '../../blocks/core/log';
import { Brand, IBrand } from '../../blocks/models/brand';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-admin-brand',
  templateUrl: './admin-brand.component.html',
  styleUrls: ['./admin-brand.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminBrandComponent implements OnInit, OnDestroy {
  rightControls: Array<Control> = [];

  routeParams: Params;
  routeData: Data;

  saveButton: Control;
  cancelButton: Control;
  brand: Brand;

  newDomainValue: string;
  newDomainFieldFocused: boolean;

  MetaDataType = MetaDataType;

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService,
    public ngxSmartModalService: NgxSmartModalService
  ) {}

  ngOnInit() {
    this.createButtons();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        if (this.routeData.type == 'create') {
          this.brand = new Brand({
            siteName: '',
            description: '',
            primaryLogoUrl: '',
            secondaryLogoUrl: '',
            favIconUrl: '',
            primaryColor: '#2574db',
            replyEmail: '',
            helpURL: '',
            privacyPolicyURL: '',
            domains: [],
            dateCreatedUTC: new Date(new Date().toUTCString()),
            dateModifiedUTC: new Date(new Date().toUTCString()),
          } as IBrand);
        } else {
          this.loadItems();
        }
      })
    );
  }

  private createButtons = () => {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);

    this.saveButton.setTranslation(this.translateService.instant('Common.Save'));
    this.cancelButton.setTranslation(this.translateService.instant('Common.Cancel'));
  };

  CreateField = (label: string, required: boolean = false, type = MetaDataType.Text): FieldDefinition => {
    return new FieldDefinition({
      type: type,
      label: this.translateService.instant(label),
      placeholder: this.translateService.instant(label),
      required: required,
    });
  };

  onAddDomain = (): void => {
    if (!this.brand) return;
    if (!this.brand.domains) this.brand.domains = [];

    this.brand.domains.push(this.newDomainValue);
    this.newDomainValue = '';
    this.newDomainFieldFocused = true;
  };

  onDeleteDomain = (index: number): void => {
    if (!this.brand || !this.brand.domains) return;

    this.brand.domains.splice(index, 1);
  };

  onDomainInputChange = (value): void => {
    this.newDomainValue = value;
  };

  hasColumns = (): boolean => {
    return this.brand && this.brand.domains && this.brand.domains.length > 0;
  };

  onReturn = (value): void => {
    if (value.trim || value.trim() != '') this.onAddDomain();
  };

  onSave = () => {
    if (this.brand.id) {
      this.hydraApi.updateBrand(this.brand, this.brandSaved, this.brandSaveFailed);
    } else {
      this.hydraApi.createBrand(this.brand, this.brandSaved, this.brandSaveFailed);
    }

    this.spinner.showSavingSpinner();
  };

  brandSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translateService.instant('admin-brand.saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-brand.validation-errors');
    }
    this.spinner.hide();
  };

  brandSaveFailed = (error: any) => {
    this.toastr.error(this.translateService.instant('admin-brand.save-failed'));
    Log.error('admin-brand.save-failed', error);
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'settings/BambaAdministratorApplication/BambaAdministratorApplication-Upland.Bamba.Application.Settings.Brands'
    );
  };

  brandLoaded = (brand: any): void => {
    this.brand = brand;
    this.spinner.hide();
  };

  brandFailed = (error: any): void => {
    this.toastr.error(this.translateService.instant('admin-brand.load-failed'));
    Log.error('admin-brand.load-failed', error);
    this.spinner.hide();
  };

  loadItems = (): void => {
    if (this.routeData.type === 'properties') {
      this.spinner.show(this.translateService.instant('admin-brand.loading-brand'), document.body);
      this.hydraApi.getBrand(this.routeParams.brandid, this.brandLoaded, this.brandFailed);
    }
  };

  onColorChanged = (color: string) => {
    this.brand.primaryColor = color;
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
